  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% - 20px);
  /* Adjust width to leave space on both sides */
  max-width: 100%;
  /* Ensure it doesn't exceed the container width */
}

#editor {
  width: 100%;
  height: 150px;
  margin-bottom: 10px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
}

#output {
  width: 100%;
  height: 150px;
  font-size: 14px;
  /* Add space below the terminal */
  display: flex;
  flex-direction: column;
  background-color: #1d1a1a;
  color: #fff3f3;
}

#sandbox-pause,
#sandbox-deploy,
#sandbox-collapse,
#sandbox-fullscreen {
  background-color: #ffcc00;
  color: #000000;
  border: 3px solid #ff6600;
  /* Adjust border color for pause */
  border-radius: 0;
  padding: 8px 16px;
  font-family: 'Press Start 2P', cursive;
  font-size: 14px;
  text-shadow: 1px 1px #ff6600;
  transition: transform 0.1s;
}

.flex {
  display: flex;
}

.flex-col {
  flex-direction: column;
}

.flex-row {
  flex-direction: row;
}

.gap-4 {
  gap: 16px;
}

.justify-stretch {
  justify-content: stretch;
}

.w-full {
  width: 100%;
}

.w-60 {
  width: 60%;
}

#rootDiv:has(#main-menu) {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#main-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 60%;
}

#h1-SS {
  font-size: 100px;
  font-family: 'Press Start 2P', cursive;
  color: #6e1ca5;
  text-shadow: 1px 1px #000000;
  margin: 0;
}

.button {
  border-radius: 0;
  padding: 8px 16px;
  font-family: 'Press Start 2P', cursive;
  font-size: 32px;
  transition: transform 0.1s;
}

.button:hover {
  transform: scale(0.95);
}

.button.blue {
  background-color: #4469e0;
  color: #000000;
  border: 3px solid #7279b8;
}

.button.blue:hover {
  background-color: #7279b8;
}

.button.yellow {
  background-color: #ffcc00;
  color: #000000;
  border: 3px solid #ff6600;
  text-shadow: 1px 1px #ff6600;
}

.button.yellow:hover {
  transform: scale(0.95);
  background-color: #ff6600;
}

#sandbox-button,
#level-select {
  border-radius: 0;
  width: 60%;
}

#go-fullscreen {
  width: calc(50% - 16px);
}

#level-one {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background-color: #4469e0;
  color: #000000;
  border: 3px solid #7279b8;
  border-radius: 0;
  width: 100%;
  padding: 8px 16px;
  font-family: 'Press Start 2P', cursive;
  font-size: 48px;
  text-shadow: 1px 1px #4c59d1;
  transition: transform 0.1s;
}

#level-one:hover {
  transform: scale(0.95);
  background-color: #7279b8;
}

#level-two {
  display: flex;
  flex-direction: column;
  gap: 8px;
  background-color: #4469e0;
  color: #000000;
  border: 3px solid #7279b8;
  border-radius: 0;
  width: 100%;
  padding: 8px 16px;
  font-family: 'Press Start 2P', cursive;
  font-size: 48px;
  text-shadow: 1px 1px #4c59d1;
  transition: transform 0.1s;
}

#level-two:hover {
  transform: scale(0.95);
  background-color: #7279b8;
}

#sandbox-back {
  background-color: #ce3434;
  color: #000000;
  border: 3px solid #9c0000;
  /* Adjust border color for pause */
  border-radius: 0;
  padding: 8px 16px;
  font-family: 'Press Start 2P', cursive;
  font-size: 14px;
  text-shadow: 1px 1px #000000;
  transition: transform 0.1s;
}

#switch {
  background-color: #000000;
  color: #ffffff;
  border: 3px solid #1d1a1a;
  /* Adjust border color for pause */
  border-radius: 0;
  padding: 8px 16px;
  font-family: 'Press Start 2P', cursive;
  font-size: 14px;
  text-shadow: 1px 1px #1d1a1a;
  transition: transform 0.1s;
}

#switch:hover {
  transform: scale(0.95);
  background-color: #35312e;
}

#go-fullscreen,
#toggle-physics {
  font-size: 16px;
  flex: 1;
}

#sandbox-fullscreen:hover {
  transform: scale(0.95);
  background-color: #ff6600;
}

#sandbox-collapse:hover {
  transform: scale(0.95);
  background-color: #ff6600;
}

#sandbox-pause:hover {
  transform: scale(0.95);
  background-color: #ff6600;
}

#sandbox-deploy {
  background-color: #0fb647;
  border: 3px solid #032c13;
  /* Adjust border color for deploy */
  text-shadow: 1px 1px #032c13;
}

#sandbox-deploy:hover {
  transform: scale(0.95);
  background-color: #ff6600;
